import React from 'react';
import { useEffect, useState } from 'react';

const useCountDown = (targetDate) => {
  const countDownDate = new Date(targetDate).getTime();

  const [countDown, setCountDown] = useState(countDownDate - new Date().getTime());

  useEffect(() => {
    const interval = setInterval(() => {
      setCountDown(countDownDate - new Date().getTime());
    }, 1000);

    return () => clearInterval(interval);
  }, [countDownDate]);

  return getReturnValues(countDown);
};

const getReturnValues = (countDown) => {
  // calculate time left
  const days = Math.floor(countDown / (1000 * 60 * 60 * 24));
  const hours = Math.floor((countDown % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
  const minutes = Math.floor((countDown % (1000 * 60 * 60)) / (1000 * 60));
  const seconds = Math.floor((countDown % (1000 * 60)) / 1000);

  return [days, hours, minutes, seconds];
};

const DateTimeDisplay = ({ id, value, type }) => {
  return (
    <div className={id}>{value}{type}</div>
  );
};

const ExpiredNotice = () => {
  return (
    <div className="expired-notice">
      <span>Lleg la hora!</span>
    </div>
  );
};

const ShowCounter = ({ days, hours, minutes, seconds, isMobile, transform }) => {
  return (
    <div className={(isMobile) ? "mobile_counter" : "desktop_counter"} style={{transform}}>
      <DateTimeDisplay id="count_days" type="d" value={days} /> <DateTimeDisplay id="count_hours" type="h" value={hours} /> <DateTimeDisplay id="count_minutes" type="m" value={minutes} /> <DateTimeDisplay id="count_secs" type="s" value={seconds} />
    </div>
  );
};

const CountDownTimer = ({ targetDate, isMobile, transform }) => {
  const [days, hours, minutes, seconds] = useCountDown(targetDate);

  if (days + hours + minutes + seconds <= 0) {
    return <ExpiredNotice />;
  } else {
    return <ShowCounter days={days} hours={hours} minutes={minutes} seconds={seconds} isMobile={isMobile} transform={transform}/>;
  }
};

export default CountDownTimer;